import { Component } from '@angular/core';
import { NavController, AlertController, ActionSheetController } from 'ionic-angular';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  shizzles: FirebaseListObservable<any>;

  constructor(public navCtrl: NavController, public alertCtrl: AlertController,
    af: AngularFireDatabase, public actionSheetCtrl: ActionSheetController) {
    this.shizzles = af.list('/shizzles');}

    //create shizzle
    Shizzles() {
    let prompt = this.alertCtrl.create({
      title: 'Shizzle Name',
      message: "Enter a name for this new album you're so keen on adding",
      inputs: [
        {
          name: 'title',
          placeholder: 'Title'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            this.shizzles.push ({
              title: data.title
            });
          }
        }
      ]
    });
    prompt.present();
  }


  //delete shizzle
  deleteShizzle(shizzleId: string) {
    this.shizzles.remove(shizzleId);
  }

  //update shizzle
  updateShizzle(shizzleId, shizzleTitle) {
    let prompt = this.alertCtrl.create({
      title: 'Shizzle Name',
      message: 'Update the name for this shizzle',
      inputs: [
        {
          name: 'title',
          placeholder: 'Title',
          value: shizzleTitle
        },
      ],
      buttons: [
        {
          text: 'cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            this.shizzles.update(shizzleId, {
              title: data.title
            });
          }
        }
      ]
    });
    prompt.present();
  }

  //delete & update shizzle actionsheet
  showOptions(shizzleId, shizzleTitle) {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'What do you want to do?',
      buttons: [
        {
          text: 'Delete Shizzle',
          role: 'destructive',
          handler: () => {
            this.deleteShizzle(shizzleId);
          }
        },{
          text: 'Update Shizzle',
          handler: () => {
            this.updateShizzle(shizzleId, shizzleTitle);
          }
        },{
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

}



